import React, { Component } from 'react';
import './OinkFooter.scss';
import OinkFooterItem from './OinkFooterItem';

class OinkFooter extends Component {

  render() {

    let FooterList; 
    
    if(this.props.footerItems){
      FooterList = this.props.footerItems.map( footerTitle => {
        return <OinkFooterItem key={footerTitle} title={footerTitle} />
      })
    }
    
    return (
          <ul className="OinkFooter">
           {FooterList}
          </ul>
    );
  }
}

export default OinkFooter;



