import React, {Component} from 'react'; 
import './OinkHighlights.scss'
class OinkHighlights extends Component {

    render() {
        return (
            <div className="OinkHighlights">
            <img className="OinkHighlights--citizens" src="images/citizens.png" />
            <img className="OinkHighlights--boa" src="images/boa.png" />
            <img className="OinkHighlights--capital" src="images/capital.png" />
            </div> 
        )
    }

}

export default OinkHighlights;


