import React, {Component} from 'react';
import Configuration from '../../Config/configuration';
import './OinkLogo.scss';
class OinkLogo extends Component {
    constructor(props){
        super(props)
        this.state = {
            defaultTheme: 'light'
        }
    }

    render(){
        const imgTheme = (this.props.theme) ? this.props.theme : this.state.defaultTheme; 
        return (<img src={`images/oink-logo-${imgTheme}.png`} alt={`${Configuration.companyName} Logo`} className="createAccount-logo" />)
    }
}

export default OinkLogo


