import React, {Component} from 'react';
import './OinkButton.scss';
class OinkButton extends Component {

    render() {
        let theme = (this.props.theme)? this.props.theme : 'light';
        let classUsed = (theme==='dark')? 'OinkButton-dark':'OinkButton-light';
        return (
            <button class={classUsed}>{this.props.text}</button>
        )
    }
}

export default OinkButton


