import React, { Component } from 'react';
import './App.scss';
import OinkButton from './Components/OinkButton';
/* Inputs */ 
import Configuration from './Config/configuration';

/* Components */ 
import  Navigation     from './Components/OinkNavigation';
import  OinkHero       from './Components/OinkHero'
import  OinkHighlights from './Components/OinkHighlights'
import  OinkBonusList  from './Components/OinkBonusList';
import  OinkFooter     from './Components/OinkFooter';
import  OinkLogo       from './Components/OinkLogo';
import  OinkForm       from './Components/OinkForm'
class App extends Component {

  constructor(props) {
    super(props);
  }

  render() {
    const CreateAccountInputList = [
      {
        'description': 'First Name',
        'placeholder': 'John',
          'inputType': 'text',
      },
      {
        'description': 'Last Name',
        'placeholder': 'Doe',
          'inputType': 'text',
      },
      {
        'description': 'Email',
        'placeholder': `email@${Configuration.companyName}.com`,
          'inputType': 'text',
      },
      {
        'description': 'Password',
        'placeholder': 'Enter your password',
          'inputType': 'password',
      }
      ];

      const LoginInputList = [
        {
          'description': 'Email',
          'placeholder': `email@${Configuration.companyName}.com`,
            'inputType': 'text',
        },
        {
          'description': 'Password',
          'placeholder': 'Enter your password',
            'inputType': 'password',
        }
        ];

        const ForgotPasswordList = [
          {
            'description': 'Enter your email Address',
            'placeholder': `email@${Configuration.companyName}.com`,
              'inputType': 'text',
          }
          ];


    return (
      <div className="App">
        <header className="App-header">
           <img src="images/oink-logo.png" alt={Configuration.companyName} className="App-logo" />
           <Navigation navItems={Configuration.navigation} />
        </header>

        <OinkHero/>
        <OinkHighlights/>


        <OinkBonusList 
          title="Get the Free Signup Bonus List" 
          subtitle="Lorem ipsum doler um sit el at tal miracus para bellum."
          inputDescription="Email Address"
          inputPlaceholder={`example@${Configuration.companyName}.com`}
          inputButtonText="Claim Bonus List"/>


      <OinkForm inputItems={CreateAccountInputList} title="Create an Account" buttonText="Create an Account" helperText="Already have an account?" helperLinkText="Login" />
      <OinkForm inputItems={LoginInputList} title="Login" buttonText="Login" helperText="Don't have an account?" helperLinkText="Create an Account" />
      <OinkForm inputItems={ForgotPasswordList} title="Forgot your Password" buttonText="Reset my Password" helperText="Don't have an account?" helperLinkText="Create an Account" />


      <OinkFooter footerItems={Configuration.footer} />

    
      </div>

    );

  }
}

export default App;



