import React, {Component} from 'react';
import './OinkForm.scss'
import OinkInput from '../OinkInput'; 
import OinkLogo from '../OinkLogo'
import OinkButton from '../OinkButton'

class OinkForm extends Component {
    constructor(prop){
        super(prop)
    }
    render(){

        let InputList; 
    
        if(this.props.inputItems){
            InputList = this.props.inputItems.map( input => {
            return (
                 <div className="OinkForm-row">
                        <OinkInput description={input.description}
                                placeholder={input.placeholder}
                                type={input.inputType} />   
                 </div>
            )
        })
    }
    
        return (
            <div className="OinkForm-container">
                <div className="OinkForm-template">
                    <div className="OinkForm-logo">
                        <OinkLogo theme='dark' />
                    </div>
                    <div className="OinkForm-title">
                        {this.props.title}
                    </div>
                    {InputList}
                    <div class="OinkForm-button">
                        <OinkButton text={this.props.buttonText} theme="dark"></OinkButton>
                    </div>
                </div>
                <div className="OinkHelper-container">
                    <span className="OinkHelper-text"> {this.props.helperText}</span><span className="OinkHelper-action">{this.props.helperLinkText}</span>
                </div> 
            </div>
        )
    }
}

export default OinkForm


