import React, { Component } from 'react';
class OinkNavigationItem extends Component {

render() {
    let classUsed = 'OinkNavigation-item'; 
    if(this.props.title.toLowerCase()=="signup"){
        classUsed = 'OinkNavigation-item--emphasis';
    }
    return ( 
        <li className={classUsed}>{this.props.title}</li>
    );
  }
}

export default OinkNavigationItem;



