import React, {Component} from 'react';
import OinkInput from '../OinkInput';
import './OinkBonusList.scss'

class OinkBonusList extends Component {
    render(){
        return (
            <div className="OinkBonusList__container">
            <div className="OinkBonusList__template">
                <div className="OinkBonusList__title">{this.props.title}</div>
                <div className="OinkBonusList__paragraph">{this.props.subtitle}</div>

            <div className="OinkBonusList__button">
                <OinkInput  description={this.props.inputDescription} 
                            placeholder={this.props.inputPlaceholder} 
                            buttonText={this.props.inputButtonText}     />
            </div>

            </div> 
            </div>
        )
    }

}

export default OinkBonusList


