import React, { Component } from 'react';
import './OinkNavigation.scss';
import OinkNavigationItem from './OinkNavigationItem.js';

class OinkNavigation extends Component {

  render() {

    let NavList; 
    
    if(this.props.navItems){
      NavList = this.props.navItems.map( navTitle => {
        return <OinkNavigationItem key={navTitle} title={navTitle} />
      })
    }
    
    return (
          <ul className="OinkNavigation">
           {NavList}
          </ul>
    );
  }
}

export default OinkNavigation;



