import React, {Component} from 'react';
import './OinkHero.scss';
import OinkButton from '../OinkButton';
class OinkHero extends Component {

 render(){
     return (
          <div className="OinkHero">
          <div className="OinkHero-left">
            <div className="OinkHero-title">The easiest way to earn checking account signup bonuses via direct deposit</div>
            <div className="OinkHero-paragraph">
              <span class="text-emphasis">Oink</span>Direct enables you to quickly and easily earn your signup bonuses with
            many U.S. Checking Accounts that require direct deposits; all without the 
            hassel of updating your direct deposit form with your current employer.
            </div>
            <div className="OinkHero-button"><OinkButton text="Claim Up to a $500 Bonus!" /></div>
          </div>

          <div className="OinkHero-right">
            <div className="OinkHero-phone">
              <div className="OinkHero-phone-container">
                <span className="OinkHero-phone-emphasis">Congrats!</span>
                <span className="OinkHero-phone-paragraph">You earned your</span>
                <span className="OinkHero-phone-enlarged">$175</span>
                <span className="OinkHero-phone-paragraph">Signup bonus with</span>
                <span className="OinkHero-phone-emphasis">Citizens Bank!</span>
                <span className="OinkHero-phone-micro">Expect to see it in your account around December 21, 2017.</span>
                
              </div>
            </div>
          </div>
        </div>
     );
 }

}

export default OinkHero;



