import React, {Component} from 'react';
import OinkButton from '../OinkButton';
import './OinkInput.scss';

class OinkInput extends Component {
    render(){
        const button = this.props.buttonText ? (<OinkButton text={this.props.buttonText} theme="dark">{this.props.buttonText}</OinkButton>):('');
        const inputStyle = this.props.buttonText ? ('OinkInput-item-wbutton'):('OinkInput-item');
        let inputType = 'text';
        if(this.props.type) inputType= this.props.type; 
        return (
            <div className="OinkInput-container">
                <div className="OinkInput-description">{this.props.description}</div>
                <div className="OinkInput-subcontainer">
                    <input  className={inputStyle} placeholder={this.props.placeholder} type={inputType} />
                    <span className="OinkInput-button">{button}</span>
                </div>
            </div>
        )
    }

}

export default OinkInput;


